@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{width:85%;height:auto;min-height:80%;background:#303036;border-radius:1rem}@media only screen and (min-width: 568px) and (max-height: 430px){.App{width:50%}}@media only screen and (min-width: 1024px) and (max-width: 1367px) and (max-height: 840px) and (orientation: landscape){.App{width:60%}}@media only screen and (min-width: 1248px){.App{width:60%}}

*{padding:0;margin:0;box-sizing:border-box}html{font-size:45%}@media only screen and (min-width: 320px) and (max-height: 250px){html{font-size:25%}}@media only screen and (max-width: 280px){html{font-size:30%}}@media only screen and (min-width: 320px) and (max-height: 250px){html{font-size:30%}}@media only screen and (min-width: 400px){html{font-size:60%}}@media only screen and (min-width: 768px) and (orientation: portrait){html{font-size:79.5%}}@media only screen and (min-width: 1024px){html{font-size:80%}}@media only screen and (min-width: 1440px){html{font-size:100%}}@media only screen and (min-width: 3800px){html{font-size:200%}}@media only screen and (min-width: 568px) and (max-height: 430px){html{font-size:52%}}body{font-family:'Open Sans', sans-serif;width:100vw;height:100vh}#root{height:auto;min-height:100vh;width:100%;background:#18181b;display:flex;justify-content:center;align-items:center;flex-direction:column}p,span,label,input,select,div{font-size:1.8rem}button{font-size:1.6rem}p{font-style:italic}button{background:transparent;border:none;outline:none;cursor:pointer}li{list-style:none;font-size:1.6rem}a{text-decoration:none}h1{font-size:2.4rem}h2{font-size:2rem}h3{font-size:1.8rem}h4{font-size:1.6rem}h1,h2,h3,h4{font-family:'Roboto', sans-serif}.App{width:85%;height:auto;min-height:80%;background:#303036;border-radius:1rem}@media only screen and (min-width: 568px) and (max-height: 430px){.App{width:50%}}@media only screen and (min-width: 1024px) and (max-width: 1367px) and (max-height: 840px) and (orientation: landscape){.App{width:60%}}@media only screen and (min-width: 1248px){.App{width:60%}}.expense-item{display:grid;grid-template-columns:0.3fr 1fr;grid-template-rows:1fr;grid-gap:1rem;gap:1rem;padding:0.5rem;margin:1rem 0;background:#26262b;color:#e4e4e7;box-shadow:5px 5px 5px -1px rgba(0,0,0,0.4)}.expense-item h2{color:#e4e4e7;flex:1 1;margin:0 1rem}.expense-item button{background:#e4e4e7;border-radius:0.75rem;font-size:1.5rem;margin:auto}.expense-item__description{display:flex;justify-content:center;align-items:center;flex-direction:row;grid-gap:1rem;gap:1rem;flex:1 1;text-align:center}@media only screen and (min-width: 560px){.expense-item__description{display:flex;justify-content:flex-start;align-items:center;flex-direction:row;flex:1 1}}.expense-item__price{font-weight:bold;background-color:#303036;border:1px solid #6b6b76;padding:0.5rem;border-radius:1rem}@media only screen and (min-width: 560px){.expense-item__price{padding:0.5rem 1.5rem}}.expense-date{display:grid;grid-template-columns:1fr;grid-template-rows:repeat(3, auto);flex:0.25 1;justify-items:center;background:#1d1d20;padding:1rem;border-radius:0 0.5rem 0.5rem 0;border:1px solid #e4e4e7;grid-gap:0.5rem;gap:0.5rem;letter-spacing:0.05rem}.expense-date div{font-family:'Open Sans', sans-serif;font-weight:300;color:#e4e4e7}.expense-date .expense-date-month,.expense-date .expense-date-day{font-weight:bold}.expense-date .expense-date-year{font-size:1.6rem}.expense-date .expense-date-month .expense-date-day{font-size:1.8rem}.expenses{padding:1rem;width:100%}.add-expense{background:#303036;background:linear-gradient(180deg, #303036 0%, #26262b);color:#e4e4e7;height:25%}form{display:grid;grid-template-columns:1fr;grid-template-rows:1fr 0.5fr;height:100%;width:100%}form input::-webkit-outer-spin-button,form input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}form input[type='number']{-moz-appearance:textfield}.add-expense__ctrls{display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(2, 1fr);grid-gap:1rem;gap:1rem;width:95%;margin:auto;color:#e4e4e7}.form-toggle{height:10vh;background:#26262b;display:flex;justify-content:center;align-items:center;flex-direction:row}.form-toggle button{background:#f6aa28;padding:1rem 3rem;border-radius:1rem;color:#18181b;transition:all 150ms ease-in-out;box-shadow:5px 5px 15px 2px #18181b}.form-toggle button:hover{background:orange;transform:scale(1.05)}.add-expense__ctrl input{width:100%;padding:0.5rem;border-radius:0.5rem;border:none;outline:none}.add-expense__ctrl input:focus{border:1px solid #f6aa28}.add-expense__ctrl label{font-weight:bold}.add-expense__actions{width:95%;height:100%;margin:auto;display:flex;justify-content:flex-end;align-items:center;flex-direction:row;grid-gap:1.25rem;gap:1.25rem}.add-expense__actions button{padding:0.75rem;color:#e4e4e7;border-radius:0.75rem;transition:all 150ms ease-in-out;box-shadow:5px 5px 6px 2px rgba(24,24,27,0.6)}.add-expense__actions .submit{background:#25a725}.add-expense__actions .submit:hover{background:#2cc92c}.add-expense__actions .cancel{background:#e0001e}.add-expense__actions .cancel:hover{background:#ff0a2b}.app-wrapper{height:75vh;overflow-y:scroll;width:100%}.expenses-filter{width:100%;height:100%}.expenses-filter__control{display:flex;justify-content:space-between;align-items:center;flex-direction:row}.expenses-filter__control label{color:#e4e4e7}.expenses-filter__control select{border:none;outline:none;padding:0.5rem 1rem;border-radius:0.5rem;background:#6b6b76;color:#e4e4e7}.expenses-list{list-style:none;padding:0}.expenses-list__fallback{color:#e4e4e7;text-align:center;margin-top:2rem}.chart{margin-top:1rem;border-radius:1rem;background:#6b6b76;text-align:center;display:grid;grid-template-columns:repeat(12, 1fr);grid-template-rows:1fr;justify-items:center;align-items:center;height:10rem}.chart-bar{height:100%;width:35%;display:flex;justify-content:center;align-items:center;flex-direction:column}.chart-bar__inner{height:75%;width:100%;margin:auto;border:1px solid #18181b;border-radius:1rem;background:#26262b;overflow:hidden;display:flex;justify-content:flex-end;align-items:initial;flex-direction:column}.chart-bar__fill{background:orange;width:100%;transition:all 300ms ease-in-out}.chart-bar__label{font-weight:bold;font-size:1.35rem;font-style:italic;text-align:center;color:#e4e4e7}

